import styled from "styled-components";

export const AboutContainer = styled.div`
  width: 100%;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  height: 100%;
  max-height: 100%;
  overflow: hidden;

  /* El t√≠tulo h6 usa autom√°ticamente los estilos globales - no necesita override */
`;

export const AboutTextContainer = styled.div`
  margin-bottom: 1rem;
  flex-shrink: 0;

  p {
    font-family: var(--font-subtitle);
    font-size: var(--size-body);
    color: var(--color-text-primary);
    line-height: 1.6;
    margin: 0;
  }

  @media (max-width: 768px) {
    margin-bottom: 0.8rem;

    p {
      font-size: var(--size-min-para);
    }
  }
`;

export const BulletsContainer = styled.div`
  /* Sin fondo glassy - contenedor limpio */
  flex: 1;
  min-height: 0;
  padding: 0.5rem 0;

  /* Estilos para ScrollStack */
  .about-scroll-stack {
    height: 500px; /* √Årea mucho m√°s grande y visible */
    max-height: 100%;
    overflow: hidden; /* El ScrollStack maneja su propio scroll */
    border-radius: 1rem;
    position: relative;

    /* Indicador visual del √°rea scrolleable */
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      border: 2px dashed rgba(255, 255, 255, 0.1);
      border-radius: 1rem;
      pointer-events: none;
      z-index: 0;
    }

    .scroll-stack-card.bullet-card {
      background: linear-gradient(135deg, rgba(15, 23, 42, 0.95), rgba(30, 41, 59, 0.9));
      border: 1px solid rgba(148, 163, 184, 0.3);
      border-radius: 0.75rem;
      padding: 0.9rem 1.2rem; /* M√°s compacto como originales */
      margin-bottom: 0.75rem;
      backdrop-filter: blur(12px);
      box-shadow: 0 4px 16px rgba(0, 0, 0, 0.4), inset 0 1px 0 rgba(255, 255, 255, 0.1);
      position: relative;
      z-index: 1;

      /* Efecto de seguridad - borde con patr√≥n */
      &::before {
        content: "";
        position: absolute;
        top: -1px;
        left: -1px;
        right: -1px;
        bottom: -1px;
        background: linear-gradient(
          45deg,
          rgba(34, 197, 94, 0.2) 0%,
          rgba(59, 130, 246, 0.2) 25%,
          rgba(168, 85, 247, 0.2) 50%,
          rgba(239, 68, 68, 0.2) 75%,
          rgba(34, 197, 94, 0.2) 100%
        );
        background-size: 8px 8px;
        border-radius: 0.75rem;
        z-index: -1;
        opacity: 0;
        transition: opacity 0.3s ease;
      }

      /* Indicador de seguridad */
      &::after {
        content: "üîí";
        position: absolute;
        top: 0.5rem;
        right: 0.75rem;
        font-size: 0.75rem;
        opacity: 0.6;
        filter: grayscale(1) brightness(0.8);
      }

      &:hover {
        background: linear-gradient(135deg, rgba(15, 23, 42, 1), rgba(30, 41, 59, 0.95));
        border-color: rgba(34, 197, 94, 0.5);
        box-shadow: 0 8px 25px rgba(0, 0, 0, 0.5), 0 0 20px rgba(34, 197, 94, 0.1),
          inset 0 1px 0 rgba(255, 255, 255, 0.2);
        transform: translateY(-1px);

        &::before {
          opacity: 1;
        }

        &::after {
          filter: grayscale(0) brightness(1.2);
          opacity: 1;
        }
      }

      p {
        color: var(--color-text-primary);
        font-family: var(--font-base);
        font-size: var(--size-body);
        line-height: 1.5; /* M√°s compacto */
        margin: 0;
        font-weight: 400;
        text-shadow: 0 1px 3px rgba(0, 0, 0, 0.3);
        padding-right: 1.5rem; /* Espacio para el icono */
      }
    }
  }

  /* Estilos personalizados para AnimatedList adaptados a nuestra web */
  .about-bullets-list {
    height: 100%;
    width: 100%;

    .scroll-list-container {
      width: 100%;
    }

    .scroll-list {
      max-height: 100%;
      padding: 0;
      overflow-y: auto;

      /* Scrollbar personalizado */
      &::-webkit-scrollbar {
        width: 4px;
      }

      &::-webkit-scrollbar-track {
        background: transparent;
      }

      &::-webkit-scrollbar-thumb {
        background: rgba(255, 255, 255, 0.2);
        border-radius: 2px;
      }
    }

    .item {
      background: linear-gradient(135deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.05));
      border: 1px solid rgba(255, 255, 255, 0.1);
      border-radius: 0.75rem;
      padding: 1rem 1.25rem;
      margin-bottom: 0.75rem;
      backdrop-filter: blur(10px);
      transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);

      &:hover {
        background: linear-gradient(135deg, rgba(255, 255, 255, 0.15), rgba(255, 255, 255, 0.08));
        border-color: rgba(255, 130, 92, 0.3);
        transform: translateY(-2px);
        box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2);
      }

      &.selected {
        background: linear-gradient(135deg, rgba(255, 130, 92, 0.2), rgba(255, 130, 92, 0.1));
        border-color: rgba(255, 130, 92, 0.4);
        transform: translateY(-1px);
      }
    }

    .item-text {
      color: var(--color-text-primary);
      font-family: var(--font-base);
      font-size: var(--size-body);
      line-height: 1.5;
      margin: 0;
      font-weight: 400;
    }
  }

  @media (max-width: 768px) {
    /* Estilos responsive para AnimatedList */
    .about-bullets-list {
      .item {
        padding: 0.8rem 1rem;
        margin-bottom: 0.5rem;
      }

      .item-text {
        font-size: var(--size-min-para);
        line-height: 1.4;
      }
    }

    /* Estilos responsive para ScrollStack */
    .about-scroll-stack {
      height: 400px; /* √Årea visible tambi√©n en mobile */

      .scroll-stack-card.bullet-card {
        padding: 0.75rem 1rem; /* Compacto en mobile */
        margin-bottom: 0.6rem;

        &::after {
          top: 0.4rem;
          right: 0.6rem;
          font-size: 0.65rem;
        }

        p {
          font-size: var(--size-min-para);
          line-height: 1.4;
          padding-right: 1.2rem;
        }
      }
    }
  }
  @media (max-width: 480px) {
    padding: 0.8rem;
  }
`;

export const BulletItem = styled.div`
  font-family: var(--font-base);
  font-size: var(--size-body);
  color: var(--color-text-primary);
  padding: 0.75rem 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  position: relative;
  padding-left: 1.5rem;

  &:last-child {
    border-bottom: none;
  }

  &:before {
    content: "‚Ä¢";
    color: var(--color-secondary);
    font-weight: bold;
    position: absolute;
    left: 0;
    top: 0.75rem;
  }

  @media (max-width: 768px) {
    font-size: var(--size-min-para);
    padding: 0.5rem 0;
    padding-left: 1.2rem;

    &:before {
      top: 0.5rem;
    }
  }
`;
